.containerWeatherList {
  min-height: 175px;

  .title {
    font-weight: 400;
    font-size: 20px;
    margin-bottom: 20px;
  }

  .byDatesList {
    display: flex;
    gap: 24px;
    flex-wrap: wrap;

    .byDatesItem {
      width: 64px;
      display: flex;
      flex-direction: column;
      align-items: center;

      .byDatesDay {
        font-weight: 400;
        font-size: 12px;
        color: gray;
        margin-bottom: 24px;
      }

      .byDatesIcon {
        height: 65px;
        margin-bottom: 12px;
      }

      .byDatesTemp {
        font-size: 14px;
      }
    }
  }
}
